@charset "utf-8";

.kolonsport-site {
  .report {
    padding: 0 6.66vw;
    form {
      .title-form {
        margin-top: 7.066vw;
        font-size: 4vw;
        color: #1f1f1f;
      }
      h4 {
        margin-top: 9.333vw;
        padding-bottom: 2.666vw;
        font-size: 4vw;
        font-weight: 900;
        color: #1f1f1f;
        border-bottom: .533vw solid #1f1f1f;
        input {
          &[type="checkbox"] {
            display: inline-block;
            width: 4.533vw;
            height: 4.533vw;
            background: url('/_ui/img/kolonsport/m/icn/icn-check-on.png') no-repeat center;
            background-size: contain;
            &:checked {
              background: url('/_ui/img/kolonsport/m/icn/icn-check-off.png') no-repeat center;
              background-size: contain;
            }
          }
        }
      }
      .small-info {
        display: block;
        margin-top: 1.333vw;
        font-size: 3.2vw;
        color: #89898c;
        line-height: 3.6vw;
      }
    }
    .report-form {
      dl {
        border-bottom: 1px solid #f1f1f1;
      }
      dt,
      dd {
        display: inline-block;
        padding: 3.33vw 0;
        font-size: 4vw;
        color: #1f1f1f;
        vertical-align: middle;
      }
      dt {
        width: 20vw;
      }
      dd {
        width: 64.8vw;
        input {
          font-size: 4vw;
          letter-spacing: -1.6px;
          &::-webkit-input-placeholder {
            color: #757575;
          }
          &::-moz-placeholder {
            color: #757575;
          }
          &:-ms-input-placeholder {
            color: #757575;
          }
          &:-moz-placeholder {
            color: #757575;
          }
          &[type="file"] {
            display: none;
          }
          &[type="radio"] {
            display: inline-block;
            position: relative;
            width: 2.933vw;
            height: 2.933vw;
            border-radius: 0;
            border: 0;
            vertical-align: middle;
            -webkit-box-sizing: border-box;
            box-sizing: border-box;
            background: #fff url('/_ui/img/kolonsport/m/icn/icn-radio-off.png') no-repeat center;
            background-size: contain;
            -webkit-appearance: none;
            -moz-appearance: none;
            appearance: none;
            &:checked {
              background: #fff url('/_ui/img/kolonsport/m/icn/icn-radio-on.png') no-repeat center;
              background-size: contain;
            }
          }
        }
        select {
          font-size: 4vw;
          color: #757575;
        }
        textarea {
          width: 100%;
          height: 23.33vw;
          padding: 1.33vw;
          resize: none;
          outline: none;
          border: .266vw solid #bfbfbf;
          box-sizing: border-box;
        }
      }
      .required {
        display: inline-block;
        width: 1.6vw;
        height: 1.6vw;
        margin-left: 1.066vw;
        background-color: #f23d4e;
        border-radius: 50%;
        vertical-align: middle;
        font: 0 / 0 a;
      }
      .col-1 {
        width: 100%;
      }
      .col-2 {
        width: 62%;
      }
      .col-3 {
        width: 40%;
      }
      .radio-txt {
        font-size: 3.2vw;
        color: #757575;
      }
      .btn-line {
        display: inline-block;
        padding: .8vw 2.4vw 1.333vw;
        font-size: 3.466vw;
        color: #1f1f1f;
        border: .266vw solid #bfbfbf;
      }
    }
    .agree-detail {
      font-size: 4vw;
      color: #757575;
      letter-spacing: -1.3px;
      word-break: keep-all;
      p {
        padding: 3.733vw 0 5.333vw;
      }
    }
    .buttons {
      padding: 12.533vw 3.733vw 13.6vw;
      .btn {
        display: block;
        width: 100%;
        height: 9.066vw;
        line-height: 9.066vw;
        font-size: 4vw;
        color: #fff;
        background: #717171;
        text-align: center;
      }
    }
  }
}
@media (min-width: 750px) {
  .kolonsport-site {
    .report {
      padding: 0 50px;
      form {
        .title-form {
          margin-top: 53px;
          font-size: 30px
        }
        h4 {
          margin-top: 70px;
          padding-bottom: 20px;
          font-size: 30px;
          border-bottom: 4px solid #1f1f1f;
          input {
            &[type="checkbox"] {
              width: 34px;
              height: 34px;
            }
          }
        }
        .small-info {
          margin-top: 10px;
          font-size: 24px;
          line-height: 27px;
        }
      }
      .report-form {
        dt,
        dd {
          padding: 25px 0;
          font-size: 30px;
        }
        dt {
          width: 150px;
        }
        dd {
          width: 487px;
          input {
            font-size: 30px;
            &[type="radio"] {
              width: 22px;
              height: 22px;
            }
          }
          textarea {
            height: 175px;
            padding: 10px;
            border: 2px solid #bfbfbf;
          }
        }
        .required {
          width: 12px;
          height: 12px;
          margin-left: 8px;
        }
        .radio-txt {
          font-size: 24px;
        }
        .btn-line {
          padding: 6px 18px 10px;
          font-size: 26px;
          border: 2px solid #bfbfbf;
        }
      }
      .agree-detail {
        font-size: 30px;
        p {
          padding: 28px 0 40px;
        }
      }
      .buttons {
        padding: 94px 28px 102px;
        .btn {
          height: 68px;
          line-height: 68px;
          font-size: 30px;
        }
      }
    }
  }
}